  font-size: $swal2-toast-validation-font-size;\n    }\n\n    .swal2-footer {\n      margin: $swal2-toast-footer-margin;\n      padding: $swal2-toast-footer-margin;\n      font-size: $swal2-toast-footer-font-size;\n    }\n\n    .swal2-close {\n      grid-column: 3/3;\n      grid-row: 1/99;\n      align-self: center;\n      width: $swal2-toast-close-button-width;\n      height: $swal2-toast-close-button-height;\n      margin: $swal2-toast-close-button-margin;\n      font-size: $swal2-toast-close-button-font-size;\n    }\n\n    .swal2-html-container {\n      margin: $swal2-toast-html-container-margin;\n      padding: $swal2-toast-html-container-padding;\n      font-size: $swal2-toast-html-container-font-size;\n      text-align: initial;\n\n      &:empty {\n        padding: 0;\n      }\n    }\n\n    .swal2-loader {\n      grid-column: 1;\n      grid-row: 1/99;\n      align-self: center;\n      width: 2em;\n      height: 2em;\n      margin: .25em;\n    }\n\n    .swal2-icon {\n      grid-column: 1;\n      grid-row: 1/99;\n      align-self: center;\n      width: 2em;\n      min-width: 2em;\n      height: 2em;\n      margin: 0 .5em 0 0;\n\n      .swal2-icon-content {\n        display: flex;\n        align-items: center;\n        font-size: $swal2-toast-icon-font-size;\n        font-weight: bold;\n      }\n\n      &.swal2-success {\n        .swal2-success-ring {\n          width: 2em;\n          height: 2em;\n        }\n      }\n\n      &.swal2-error {\n        [class^='swal2-x-mark-line'] {\n          top: .875em;\n          width: 1.375em;\n\n          &[class$='left'] {\n            left: .3125em;\n          }\n\n          &[class$='right'] {\n            right: .3125em;\n          }\n        }\n      }\n    }\n\n    .swal2-actions {\n      justify-content: flex-start;\n      height: auto;\n      margin: 0;\n      margin-top: .3125em;\n      padding: 0;\n    }\n\n    .swal2-styled {\n      margin: .25em .5em;\n      padding: .4em .6em;\n      font-size: $swal2-toast-buttons-font-size;\n\n      &:focus {\n        box-shadow: $swal2-toast-button-focus-box-shadow;\n      }\n    }\n\n    .swal2-success {\n      border-color: $swal2-success;\n\n      [class^='swal2-success-circular-line'] { // Emulate moving circular line\n        position: absolute;\n        width: 1.6em;\n        height: 3em;\n        transform: rotate(45deg);\n        border-radius: 50%;\n\n        &[class$='left'] {\n          top: -.8em;\n          left: -.5em;\n          transform: rotate(-45deg);\n          transform-origin: 2em 2em;\n          border-radius: 4em 0 0 4em;\n        }\n\n        &[class$='right'] {\n          top: -.25em;\n          left: .9375em;\n          transform-origin: 0 1.5em;\n          border-radius: 0 4em 4em 0;\n        }\n      }\n\n      .swal2-success-ring {\n        width: 2em;\n        height: 2em;\n      }\n\n      .swal2-success-fix {\n        top: 0;\n        left: .4375em;\n        width: .4375em;\n        height: 2.6875em;\n      }\n\n      [class^='swal2-success-line'] {\n        height: .3125em;\n\n        &[class$='tip'] {\n          top: 1.125em;\n          left: .1875em;\n          width: .75em;\n        }\n\n        &[class$='long'] {\n          top: .9375em;\n          right: .1875em;\n          width: 1.375em;\n        }\n      }\n\n      &.swal2-icon-show {\n        @if $swal2-icon-animations {\n          .swal2-success-line-tip {\n            animation: swal2-toast-animate-success-line-tip .75s;\n          }\n\n          .swal2-success-line-long {\n            animation: swal2-toast-animate-success-line-long .75s;\n          }\n        }\n      }\n    }\n\n    &.swal2-show {\n      animation: $swal2-toast-show-animation;\n    }\n\n    &.swal2-hide {\n      animation: $swal2-toast-hide-animation;\n    }\n  }\n}\n","$swal2-white:         #fff !default;\n$swal2-black:         #000 !default;\n$swal2-outline-color: rgba(100, 150, 200, .5) !default;\n\n// CONTAINER\n$swal2-container-padding: .625em !default;\n\n// BOX MODEL\n$swal2-width: 32em !default;\n$swal2-padding: 0 0 1.25em !default;\n$swal2-border: none !default;\n$swal2-border-radius: 5px !default;\n$swal2-box-shadow: #d9d9d9 !default;\n\n// ANIMATIONS\n$swal2-show-animation: swal2-show .3s !default;\n$swal2-hide-animation: swal2-hide .15s forwards !default;\n\n// BACKGROUND\n$swal2-background: $swal2-white !default;\n\n// TYPOGRAPHY\n$swal2-font: inherit !default;\n$swal2-font-size: 1rem !default;\n\n// BACKDROP\n$swal2-backdrop: rgba($swal2-black, .4) !default;\n$swal2-backdrop-transition: background-color .1s !default;\n\n// ICONS\n$swal2-icon-size: 5em !default;\n$swal2-icon-animations: true !default;\n$swal2-icon-margin: 2.5em auto .6em !default;\n$swal2-icon-font-family: inherit !default;\n$swal2-icon-font-size: 3.75em !default;\n$swal2-icon-border-color: #000 !default;\n$swal2-icon-zoom: null !default;\n$swal2-success: #a5dc86 !default;\n$swal2-success-border: rgba($swal2-success, .3) !default;\n$swal2-error: #f27474 !default;\n$swal2-warning: #f8bb86 !default;\n$swal2-info: #3fc3ee !default;\n$swal2-question: #87adbd !default;\n\n// IMAGE\n$swal2-image-margin: 2em auto 1em !default;\n\n// TITLE\n$swal2-title-position: relative !default;\n$swal2-title-max-width: 100% !default;\n$swal2-title-margin: 0 !default;\n$swal2-title-padding: .8em 1em 0 !default;\n$swal2-title-color: lighten($swal2-black, 35) !default;\n$swal2-title-font-size: 1.875em !default;\n$swal2-title-font-weight: 600 !default;\n$swal2-title-text-align: center !default;\n\n// HTML CONTAINER\n$swal2-html-container-justify-content: center !default;\n$swal2-html-container-margin: 0 !default;\n$swal2-html-container-padding: 1em 1.6em .3em !default;\n$swal2-html-container-color: lighten($swal2-black, 33) !default;\n$swal2-html-container-font-size: 1.125em !default;\n$swal2-html-container-font-weight: normal !default;\n$swal2-html-container-line-height: normal !default;\n$swal2-html-container-text-align: center !default;\n$swal2-html-container-word-wrap: break